import Vue from 'vue'
import Router from 'vue-router'
//首页
const Index = () => import('@/components/Index')
//登录页
const Login = () => import('@/components/Login')
//修改设备页面
const EditServer = () => import('@/components/EditServer')
//设备管理服务页面
const ServerService = () => import('@/components/ServerService')
//负责人管理页
const Users = () => import('@/components/Users')
//测试页面
const Test = () => import('@/components/Test')
//故障页面
const Fault = () => import('@/components/Fault')
//检测页面
const ManualTest = () => import('@/components/ManualTest')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      children:[
        {
          path:'/users',
          name:'Users',
          component:Users
        },
        {
          path:'/fault',
          name:'Fault',
          component:Fault
        },
        {
          path:'/updateServer',
          name: 'EditServer',
          component: EditServer
        },
        {
          path:'/server_service',
          name: 'ServerService',
          component: ServerService
        },
        {
          path:'/test',
          name: 'Test',
          component: Test
        },
        {
          path:'/manualTest',
          name: 'ManualTest',
          component: ManualTest
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    }
  ]
})



